import logo from "./logo.svg";
import "./App.css";
import "antd/dist/antd.css";
import LatestWeekly from "./components/LatestWeekly";
import LatestMonthly from "./components/LatestMonthly";
import EditPage from "./components/EditPage";
import MarkdownEditPage from "./components/MarkdownEditPage";
import MarkdownPage from "./components/MarkdownPage";
import ReadList from "./components/ReadList";
import ShowPage from "./components/ShowPage";
import SignInPage from "./components/SignInPage";
import ProtectedRoute from "./components/ProtectedRoute";
import EmailAnalysisList from "./components/EmailAnalysisList";
import {
  BrowserRouter as Router,
  Route,
  Link,
  useLocation,
  Redirect,
} from "react-router-dom";
import { useState, useEffect } from "react";
import { Menu, Layout } from "antd";
import ChineseCalendar from "./components/ChineseCalendar";
import {
  BookOutlined,
  UserOutlined,
  AimOutlined,
  EditOutlined,
  FolderViewOutlined,
  HeartOutlined,
  SafetyCertificateOutlined,
  CalendarOutlined,
} from "@ant-design/icons";
import ReadSummaryList from "./components/ReadSummaryList";
const { Header, Sider, Content } = Layout;
function useQuery() {
  return new URLSearchParams(useLocation().search);
}
const { SubMenu } = Menu;
function App(props) {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const authToken = localStorage.getItem("authToken");
    setIsAuthenticated(authToken !== null);
  }, []);
  const sub = [
    { name: "", json: "weekly" },
    { name: "", json: "weekly_work" },
    { name: "", json: "weekly_analysis" },
    { name: "", json: "monthly" },
    { name: "", json: "reminder_list" },
    { name: "", json: "story" },
    { name: "", json: "fun" },
    { name: "", json: "event_and_feeling" },
  ];
  const todo_list = [
    { name: "", json: "video_upload_checklist" },
    { name: "", json: "book" },
    { name: "", json: "cook" },
    { name: "", json: "tour" },
    { name: "", json: "photos" },
    { name: "", json: "play" },
  ];
  let query = useQuery();
  return (
    <Layout>
      <Sider
        breakpoint="lg"
        collapsedWidth="0"
        onBreakpoint={(broken) => {
          console.log(broken);
        }}
        onCollapse={(collapsed, type) => {
          console.log(collapsed, type);
        }}
      >
        <div className="logo" />
        <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>
          <Menu.Item key="signin" icon={<UserOutlined />}>
            Sign In
            <Link to="/signin" />
          </Menu.Item>
          <Menu.Item key="1" icon={<UserOutlined />}>
            
            <Link to="/improvement" />
          </Menu.Item>
          <Menu.Item key="10" icon={<UserOutlined />}>
            
            <Link to="/principe" />
          </Menu.Item>
          <Menu.Item key="11" icon={<HeartOutlined />}>
            
            <Link to="/empthy" />
          </Menu.Item>
          <Menu.Item key="12" icon={<SafetyCertificateOutlined />}>
            
            <Link to="/safety" />
          </Menu.Item>
          <Menu.Item key="2" icon={<AimOutlined />}>
            
            <Link to="/weekly" />
          </Menu.Item>
          <Menu.Item key="3" icon={<AimOutlined />}>
            
            <Link to="/monthly" />
          </Menu.Item>
          <Menu.Item key="4" icon={<AimOutlined />}>
            
            <Link to="/year_plan" />
          </Menu.Item>
          <Menu.Item key="calendar" icon={<CalendarOutlined />}>
            
            <Link to="/chinese-calendar" />
          </Menu.Item>
          <Menu.Item key="5" icon={<BookOutlined />}>
            
            <Link to="/read" />
          </Menu.Item>
          <Menu.Item key="6" icon={<BookOutlined />}>
            
            <Link to="/book_summary" />
          </Menu.Item>
          <Menu.Item key="7" icon={<BookOutlined />}>
            email
            <Link to="/email_analysis" />
          </Menu.Item>
          <SubMenu key="sub1" icon={<EditOutlined />} title="">
            {sub.map((item) => {
              const key = `${item.json}_edit`;
              return (
                <Menu.Item key={key}>
                  {item.name}
                  <Link to={`/${key}`} />
                </Menu.Item>
              );
            })}
          </SubMenu>
          <SubMenu key="sub2" icon={<FolderViewOutlined />} title="">
            {sub.map((item) => {
              const key = `${item.json}_view`;
              return (
                <Menu.Item key={key}>
                  {item.name}
                  <Link to={`/${key}`} />
                </Menu.Item>
              );
            })}
          </SubMenu>
          <SubMenu key="sub3" icon={<EditOutlined />} title="">
            {todo_list.map((item) => {
              const key = `${item.json}_edit`;
              return (
                <Menu.Item key={key}>
                  {item.name}
                  <Link to={`/${key}`} />
                </Menu.Item>
              );
            })}
          </SubMenu>
          <SubMenu key="sub4" icon={<FolderViewOutlined />} title="">
            {todo_list.map((item) => {
              const key = `${item.json}_view`;
              return (
                <Menu.Item key={key}>
                  {item.name}
                  <Link to={`/${key}`} />
                </Menu.Item>
              );
            })}
          </SubMenu>
        </Menu>
      </Sider>
      <Layout>
        <Header
          className="site-layout-sub-header-background"
          style={{ padding: 0 }}
        />
        <Content style={{ margin: "24px 16px 0" }}>
          <div
            className="site-layout-background"
            style={{ padding: 24, minHeight: 1024 }}
          >
            <ProtectedRoute
              exact
              path="/"
              component={() => (
                <MarkdownPage name=".md" list="must" />
              )}
            />
            <ProtectedRoute
              path="/improvement"
              component={() => (
                <MarkdownPage name=".md" list="must" />
              )}
            />
            <ProtectedRoute
              path="/safety"
              component={() => (
                <MarkdownPage name=".md" list="must" />
              )}
            />
            <ProtectedRoute
              path="/year_plan"
              component={() => (
                <MarkdownPage name="2026.md" list="must" />
              )}
            />
            <ProtectedRoute path="/weekly" component={LatestWeekly} />
            <ProtectedRoute path="/monthly" component={LatestMonthly} />
            <ProtectedRoute path="/read" component={ReadList} />
            <ProtectedRoute path="/book_summary" component={ReadSummaryList} />
            <ProtectedRoute
              path="/email_analysis"
              component={EmailAnalysisList}
            />
            {sub.map((item) => {
              const key = `/${item.json}_edit`;

              return (
                <ProtectedRoute
                  path={key}
                  children={
                    <EditPage list={item.json} date={query.get("date") ?? ""} />
                  }
                />
              );
            })}
            {sub.map((item) => {
              const key = `/${item.json}_view`;
              return (
                <ProtectedRoute
                  path={key}
                  children={<ShowPage list={item.json} />}
                />
              );
            })}
            {todo_list.map((item) => {
              const key = `/${item.json}_edit`;

              return (
                <ProtectedRoute
                  path={key}
                  children={
                    <EditPage list={item.json} date={query.get("date") ?? ""} />
                  }
                />
              );
            })}
            {todo_list.map((item) => {
              const key = `/${item.json}_view`;
              return (
                <ProtectedRoute
                  path={key}
                  children={<ShowPage list={item.json} />}
                />
              );
            })}
            <Route
              path="/markdown_edit"
              children={
                <MarkdownEditPage
                  list={query.get("list") ?? ""}
                  name={query.get("name") ?? ""}
                />
              }
            />
            <Route
              path="/markdown_show"
              children={
                <MarkdownPage
                  list={query.get("list") ?? ""}
                  name={query.get("name") ?? ""}
                />
              }
            />
            <ProtectedRoute
              path="/principe"
              children={<MarkdownPage name=".md" list="must" />}
            />
            <ProtectedRoute
              path="/empthy"
              children={<MarkdownPage name=".md" list="must" />}
            />
            <Route path="/signin" component={SignInPage} />
            <Route path="/chinese-calendar" component={ChineseCalendar} />
          </div>
        </Content>
      </Layout>
    </Layout>
  );
}

export default App;
